.stimulation .container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 300px;

  @include vp-1199 {
    margin-bottom: 150px;
  }

  @include vp-767 {
    margin-bottom: 75px;
  }
}

.stimulation__content {
  box-shadow: $container-background;
  display: flex;
  flex-direction: column;
  position: relative;
}
