.about__container {
  box-shadow: $container-background;
  margin-bottom: 200px;
  padding: 20px;

  @include vp-1199 {
    margin-bottom: 100px;
  }

  @include vp-767 {
    margin-bottom: 50px;
  }
}

.about a {
  color: $color-default-white;
  text-decoration: underline;
}

.about .container > a {
  display: flex;
  font-family: $ff-baron;
  font-size: 48px;
  line-height: 150%;
  margin: 0 auto 150px;
  position: relative;
  text-align: center;
  width: fit-content;

  & svg {
    bottom: 0;
    height: 250px;
    left: 50%;
    position: absolute;
    transform: translate(-60%, 50%);
    width: 100%;
  }
}

.about__container > p:last-of-type {
  font-size: 36px;
}

.about ul {
  padding-left: 200px;
  position: relative;

  @include vp-1199 {
    padding-left: 75px;
  }

  @include vp-767 {
    padding: 0;
  }
}

.about__link {
  & p {
    font-size: 24px;
    line-height: 150%;
    margin: 0 0 10px;
  }

  & a {
    display: flex;
    font-family: $ff-baron;
    font-size: 48px;
    line-height: 150%;
    margin: 0;
    position: relative;
    text-align: center;
    width: fit-content;
  }
}
