.container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 150px;
  position: relative;
  width: 100%;

  @include vp-1199 {
    padding: 0 100px;
  }

  @include vp-767 {
    padding: 0 30px;
  }
}

.container h2 {
  display: flex;
  font-size: 48px;
  margin: 0 auto 100px;
  position: relative;
  text-align: center;
  width: fit-content;

  @include vp-767 {
    font-size: 32px;
  }
}

.container h2 svg {
  bottom: 0;
  height: 250px;
  left: 50%;
  position: absolute;
  transform: translate(-60%, 40%);
  width: 100%;

  @include vp-767 {
    transform: translate(-60%, 50%);
  }
}

.examples h2 {
  margin-bottom: 150px;
}

.examples h2 svg {
  transform: translate(-60%, 60%);
}

.container p {
  font-size: 24px;
  line-height: 150%;
  margin: 0 0 40px;

  @include vp-1199 {
    font-size: 20px;
    margin: 0 0 20px;
  }
}
