.influence__container {
  display: flex;
  margin-bottom: 200px;

  @include vp-1199 {
    margin-bottom: 150px;
  }

  @include vp-767 {
    margin-bottom: 75px;
  }
}

.influence h2 {
  margin-bottom: 200px;

  @include vp-767 {
    & svg {
      padding: 25px;
      width: 100%;
    }
  }
}

.influence ul {
  box-shadow: 0 0 0 1000px rgba(38, 22, 74, 0.65) inset, 0 0 10px 10px rgba(38, 22, 74, 0.65);
  display: flex;
  flex-direction: column;
  gap: 40px;

  @include vp-767 {
    padding: 10px;
  }
}

.influence li {
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr 1fr;

  @include vp-1199 {
    gap: 5px;
    grid-template-columns: 200px 1fr;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}

@keyframes arrow {
  0% {
    opacity: 1;
    transform: translateX(3px);
  }

  50% {
    opacity: 0.5;
    transform: translateX(-3px);
  }

  100% {
    opacity: 1;
    transform: translateX(3px);
  }
}

.influence__title {
  align-items: center;
  display: flex;
  gap: 30px;
  justify-content: flex-end;
  padding: 10px;
  text-align: center;

  &::after {
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-name: arrow;
    animation-timing-function: $tf-linear;
    background-image: url("../img/content/arrow.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    content: "";
    display: block;
    flex-shrink: 0;
    height: 100%;
    width: 150px;
  }

  @include vp-1199 {
    gap: 15px;

    &::after {
      width: 50px;
    }
  }

  @include vp-767 {
    justify-content: flex-start;

    &::after {
      display: none;
    }
  }
}

.influence p {
  box-shadow: none;
  font-size: 24px;
  margin-bottom: 0;
  padding: 0;
}
