.background {
  background-image: url("../img/content/background/background-desktop.webp");
  background-position: center;
  background-size: cover;
  height: 100vh;
  position: fixed;
  width: 100%;
  z-index: -1;

  .container {
    height: 100%;
    position: relative;
    width: 100%;
  }

  &__planet {
    position: absolute;
    right: 0;
    top: 0;

    @include movearound(-10%, 10%, 5px, planetsvg, steps(4));

    & svg {
      height: 300px;
      width: 300px;

      @include vp-767 {
        height: 150px;
        width: 150px;
      }
    }
  }

  &__milkyway {
    bottom: 0;
    left: 0;
    position: absolute;

    @include movearound(10%, -10%, 5px, universesvg, steps(4));

    & svg {
      height: 300px;
      width: 300px;

      @include vp-767 {
        height: 150px;
        width: 150px;
      }
    }
  }
}
