.marina {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 600px;
  position: absolute;
  width: 800px;
  z-index: -1;

  @include vp-1199 {
    height: 300px;
    width: 400px;
  }

  @include vp-767 {
    display: none;
  }
}

.marina--stimulation-first {
  background-image: url("../img/content/marina/marina-stimulation-first.webp");
  bottom: 50%;
  right: 0;

  @include movearound(65%, 20%, 10px, first);

  @include vp-1199 {
    @include movearound(60%, 20%, 5px, first);
  }
}

.marina--stimulation-second {
  background-image: url("../img/content/marina/marina-stimulation-second.webp");
  bottom: 0;
  left: 0;

  @include movearound(-67%, 70%, 10px, second);

  @include vp-1199 {
    @include movearound(-65%, 70%, 5px, second);
  }
}

.marina--examples-first {
  background-image: url("../img/content/marina/marina-examples-first.webp");
  right: 0;
  top: 50%;

  @include movearound(55%, -50%, 10px, third);

  @include vp-1199 {
    @include movearound(35%, -50%, 5px, third);
  }
}

.marina--examples-second {
  background-image: url("../img/content/marina/marina-examples-second.webp");
  bottom: 0;
  left: 0;

  @include movearound(-45%, 75%, 10px, fourth);

  @include vp-1199 {
    @include movearound(-30%, 75%, 5px, fourth);
  }
}

.marina--influence-first {
  background-image: url("../img/content/marina/marina-influence-first.webp");
  right: 0;
  top: 50%;

  @include movearound(55%, -50%, 10px, five);

  @include vp-1199 {
    @include movearound(40%, -50%, 5px, five);
  }
}

.marina--influence-second {
  background-image: url("../img/content/marina/marina-influence-second.webp");
  bottom: 0;
  left: 0;

  @include movearound(-45%, 50%, 10px, six);

  @include vp-1199 {
    @include movearound(-33%, 50%, 5px, six);
  }
}

.marina--about {
  background-image: url("../img/content/marina/marina-about.webp");
  bottom: 0;
  left: 0;
  transform: translate(-45%, 50%);
  z-index: auto;

  @include movearound(-45%, 50%, 10px, seven);
}
