.intro {
  margin-bottom: 100px;
  margin-top: 100px;

  & h2 {
    font-size: 64px;

    & svg {
      bottom: 0;
      height: 100%;
      left: 50%;
      position: absolute;
      transform: translate(-60%, 40%);
      width: 100%;
    }
  }

  @include vp-1199 {
    margin-bottom: 100px;
  }
}

.intro__marina {
  aspect-ratio: 771 / 1329;
  background-image: url("../img/content/marina/marina-on-planet.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 500px;
  margin: 0 auto;

  @include vp-767 {
    width: 100%;
  }
}
